.home__title__wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 24px;
	margin: 160px 0px;
	text-align: center;
	.sub-title {
		display: flex;
		align-items: flex-end;
		h1 {
			color: $purple;
			font-size: 30px;
			font-weight: bold;
		}
	}
	.main-title {
		margin: 20px 0px;
		font-weight: bold;
	}
}
.recent_posts {
	.recent_posts__header {
		color: $header-color;
		font-family: "Helvetica Neue", "Segoe UI", Helvetica, Arial, sans-serif;
		font-size: 22px;
		font-weight: bold;
		margin: 2rem 0;
	}
	.recent_posts__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		.recent_posts__list {
			display: grid;
			grid-template-columns: repeat(3, 32%);
			grid-template-rows: repeat(2, 400px);
			grid-column-gap: 20px;
			grid-row-gap: 10px;
			font-family: "Helvetica Neue", "Segoe UI", Helvetica, Arial, sans-serif;
			width: 96%;
			.recent_post_element {
				.recent_post_lists__url {
					transition: all 0.3s ease-in-out;
					.recent_post_lists__thumbnail {
						object-fit: cover;
						width: 100%;
						height: 50%;
						transition: all 0.3s ease-in-out;
					}
					.recent_post_lists__thumbnail:hover {
						-webkit-transform: scale(1.05); /*  크롬 */
						-moz-transform: scale(1.05); /* FireFox */
						-o-transform: scale(1.05); /* Opera */
						transform: scale(1.05);
						border-radius: 4px;
					}
					.recent_post_lists__summary {
						padding-left: 4px;
						padding-top: 10px;
						.recent_post_lists__category {
							color: $purple;
							display: flex;
							font-size: 14px;
							font-weight: 500;
							margin: 10px 0;
						}
						.recent_post_lists__title {
							color: $header-color;
							font-size: 22px;
							font-weight: bold;
						}
						.recent_post_lists__excerpt {
							margin-top: 10px;
							color: $dark-grey;
							font-size: 16px;
						}
					}
				}
				.recent_post_lists__url:hover .recent_post_lists__category,
				.recent_post_lists__url:hover .recent_post_lists__title,
				.recent_post_lists__url:hover .recent_post_lists__excerpt,
				.recent_post_lists__url:hover .recent_post_lists__thumbnail {
					opacity: 70%;
				}
			}
		}
	}
}
